.Navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  background-color: transparent;
  color: #FFFFFF;
  padding: 0 2%;
  box-shadow: 0 3px 20px black;
}

.Navbar-left {
  display: flex;
  align-items: first baseline;
}

.Navbar-right {
  display: flex;
  align-items: center;
}

.Navbar-button {
  background-color: transparent;
  border: none;
  color: #FFFFFF;
  font-size: 1rem;
  font-weight: bold;
  margin-right: 10%;
  margin-left: 3%;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
  text-decoration: none;
}

.Navbar-button::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -4px;
  width: 100%;
  height: 2px;
  background-color: #1DB954;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: left center;
          transform-origin: left center;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}

.Navbar-button:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.Navbar-button:focus {
  outline: none;
}

.Navbar-login-button {
  background-color: #1DB954;
  color: white;
  border-radius: 15px;
  padding: 8px 15px;
  margin-right: 3%;
  box-shadow: #1DB954, 5px, 5px, 20px;
  text-decoration: none;
  align-items: center;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 0.75rem;
  white-space: nowrap;
  margin-left: 15%;
}

.Navbar-login-button:hover {
  padding: 9px 16px;
}

.loading-circle {
  display: inline-block;
  width: 15px;
  height: 15px;
  border: 2px solid #33CC33;
  border-top-color: transparent;
  border-radius: 50%;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
  margin-left: 5px;
  min-width: 15px;
}


@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes spin {
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.Navbar-dropdown {
  position: relative;
}

.Navbar-dropdown-toggle {
  margin-right: 2%;
  padding: 5px 10px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
}

.Navbar-dropdown-content {
  display: none;
  position: absolute;
  top: 100%;
  margin-top: 15px;
  right: 0;
  min-width: 175px;
  background-color: #1DB954;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  padding: 5px;
  border-radius: 8px;
  opacity: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 15px;
  font-weight: thin;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  transition: opacity 0.2s ease, -webkit-transform 0.2s ease;
  transition: opacity 0.2s ease, transform 0.2s ease;
  transition: opacity 0.2s ease, transform 0.2s ease, -webkit-transform 0.2s ease;
}

.Navbar-dropdown.open .Navbar-dropdown-content {
  display: block;
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.Navbar-dropdown-button:hover {
  background-color: #16893e;
}

.Navbar-dropdown-button {
  display: block;
  padding: 10px;
  text-decoration: none;
  color: #fff;
  background-color:#1DB954;
  outline: none;
  border: none;
  min-width: 175px;
  text-align: left;
  border-radius: 5px;
}

.Navbar-dropdown-checkmark {
  margin-left: 8px; /* Adjust the margin to move the checkmark further to the right */
  font-size: 12px; /* Adjust the font size to make the checkmark appear smaller */
}

.Navbar-button.beta {
  position: relative;
}

.beta-subtext {
  position: absolute;
  top: -10px;
  right: -4px;
  font-size: 10px;
  color: #FFFFFF;
  background-color: #1DB954;
  padding: 2px 4px;
  border-radius: 4px;
}

ul {
  padding: 0;
  margin: 2vh 0 0 0;
}

.ListItem {
  display: flex;
  align-items: center;
  padding: 2vw;
  font-family: sans-serif;
}

.box {
  width: 10vw;
  height: 10vw;
  margin-right: 2vw;
  background-color: #fff;
  border-radius: 10%;
}

.number {
  margin-right: 2vw;
  color: #fff;
  font-size: 3vw;
  font-weight: bold;
}

.info {
  display: flex;
  flex-direction: column;
}

.songName {
  color: #fff;
  font-size: 4vw;
}

.artist {
  color: #aaa;
  font-size: 3.5vw;
}

.ListItem:hover {
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
  background-color: rgba(255, 255, 255, 0.05);
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
}

.ListItem.selected {
  background-color: rgba(128, 128, 128, 0.2);
}

.explicit {
  display: inline-block;
  padding: 1vw;
  font-size: 2vw;
  font-weight: thin;
  color: black;
  background-color: gray;
  margin-right: 1vw;
  border-radius: 2px;
}

.duration {
  margin-right: 4vw;
  color: gray;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 3vw;
}

.popularity {
  display: inline-block;
  padding: 2vw 4vw;
  border-radius: 4px;
  font-weight: bold;
  color: white;
  background-color: var(--popularity-color);
}

.loading-circle-large {
  display: inline-block;
  width: 30vw;
  height: 30vw;
  border: 2px solid #33CC33;
  border-top-color: transparent;
  border-radius: 50%;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
  align-items: center;
  margin-left: 20vw;
  margin-top: 5vh;
  margin-bottom: 25vh;
}

.ellipsis {
  color: gray;
  align-items: flex-end;
  display: flex;
  width: 4vw;
  font-weight: bold;
  margin-right: 6vw;
}

.popup {
  position: relative;
  top: 100%;
  left: 50%;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  padding: 1vw;
  background-color: #1DB954;
  border-radius: 4px;
  color: white;
  font-size: 1.5vw;
  cursor: pointer;
  min-width: 9vw;
}

.popupText {
  font-size: 1.3vw;
  font-weight: thin;
}

.more-suggestions-button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 5vh;
  padding: 2vh 3vw;
  background-color: #1DB954;
  color: #1DB954;
  font-size: 2.5vw;
  font-weight: bold;
  border: none;
  border-radius: 10px;
  min-width: 10%;
  cursor: pointer;
  margin-left: 47%;
  margin-bottom: 2%;
}

@media (max-width: 600px) {
  .number {
    font-size: 3vw;
  }

  .info {
    flex-grow: 1;
  }

  .songName {
    font-size: 3.5vw;
  }

  .artist {
    font-size: 2.5vw;
  }

  .explicit {
    font-size: 1.5vw;
    padding: 1vw;
  }

  .duration {
    margin-right: 2vw;
    font-size: 3.5vw;
  }

  .popularity {
    padding: 2vw 4vw;
    font-size: 3.5vw;
  }

  .loading-circle-large {
    width: 40vw;
    height: 40vw;
    margin-left: 30vw;
    margin-top: 10vh;
    margin-bottom: 20vh;
  }

  .ellipsis {
    width: 3vw;
    margin-right: 2vw;
    align-items: right;
  }

  .popup {
    padding: 2vw;
    font-size: 2vw;
    min-width: 15vw;
  }

  .popupText {
    font-size: 1.5vw;
  }

  .more-suggestions-button {
    margin-top: 10vh;
    padding: 4vh 6vw;
    font-size: 4vw;
    margin-left: 40%;
    margin-bottom: 2vh;
  }
}

@media (min-width: 601px) {
  .ListItem {
    padding: 10px;
  }

  .box {
    width: 35px;
    height: 35px;
    margin-right: 10px;
  }

  .number {
    margin-right: 5px;
    width: 20px;
    font-size: 12px;
  }

  .songName {
    font-size: 14px;
  }

  .artist {
    font-size: 11px;
  }

  .explicit {
    font-size: 8px;
    padding: 2px;
    margin-right: 3px;
  }

  .info {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }

  .duration {
    margin-left: auto;
    font-size: 12px;
    margin-right: 10px;
  }

  .popularity {
    padding: 3px 6px;
    font-size: 12px;
  }

  .loading-circle-large {
    width: 150px;
    height: 150px;
    margin-left: 25%;
    margin-top: 10%;
    margin-bottom: 25%;
  }

  .ellipsis {
    margin-left: 0;
    width: 10px;
    margin-right: 15px;
  }

  .popup {
    padding: 5px;
    font-size: 10px;
    min-width: 70px;
  }

  .popupText {
    font-size: 8px;
  }

  .more-suggestions-button {
    padding: 8px 12px;
    font-size: 14px;
    margin-left: 45%;
    margin-bottom: 10px;
  }
}

.song-list {
  margin-bottom: 2%;
}



.Login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: relative;
}


.Login::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('https://images.unsplash.com/photo-1504509546545-e000b4a62425?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2370&q=80');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.4;
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
}

.Login-container {
  text-align: center;
  padding: 20px;
  position: relative;
  z-index: 1;
}

.Login-title {
  font-size: 10vw;
  margin-bottom: 5%;
  color: #FFFFFF;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.Login-button {
  background-color: #1DB954;
  color: white;
  border: none;
  border-radius: 1.5vw;
  padding: 1.5vw 1.5vw;
  font-size: 2vw;
  font-weight: thin;
  box-shadow: green, 5px, 5px, 10px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.2s ease;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}

.Login-button:hover {
  background-color: #16893e;
}

@media (min-width: 601px) {
  .Login-title {
    font-size: 8vw;
  }

  .Login-button {
    font-size: 1vw;
    padding: 1vw 1vw;
  }
}

body {
    background: linear-gradient(to bottom,#121212 2%, #164125 200%);
    margin: 0;
    padding: 0;
  }
